
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import logoDelete from '../Training/icons/delete.svg'
import { deleteBook } from '../../redux/library/operation';

export const BookList = () => {
  const books = useSelector(state => state.books.booksBD);
  const dispatch = useDispatch();

  const handleDeleteBook = bookId => {
    console.log("Deleting book with ID:", bookId);
    dispatch(deleteBook(bookId));
  };

  const planningBooks = [];
  const readingBooks = [];
  const completedBooks = [];

  //         
  books.forEach(book => {
    if (book.status === "planning") {
      planningBooks.push(book);
    } else if (book.status === "reading") {
      readingBooks.push(book);
    } else if (book.status === "completed") {
      completedBooks.push(book);
    }
  });

  // ,     
  if (!books || books.length === 0) {
    return (
      <div>
        <ul>
          <li>
            <h1> 1.</h1>
          </li>
          <li>  </li>
          <li>   ,    .</li>
        </ul>
        <ul>
          <li> 2.</li>
          <li>   </li>
          <li> ,  ,  .</li>
        </ul>
      </div>
    );
  }

  return (
    <div>
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          
          </tr>
        </thead>
        <tbody>
          {planningBooks.map(book => (
            <tr key={book.id}>
              <td>{book.title}</td>
              <td>{book.author}</td>
              <td>{book.year}</td>
              <td>{book.pages}</td>
              <td><img onClick={() => handleDeleteBook(book.id)} src={logoDelete} alt="" /></td>
            </tr>
          ))}
          {readingBooks.map(book => (
            <tr key={book.id}>
              <td>{book.title}</td>
              <td>{book.author}</td>
              <td>{book.year}</td>
              <td>{book.pages}</td>
              <td><img onClick={() => handleDeleteBook(book.id)} src={logoDelete} alt="" /></td>
            </tr>
          ))}
          {completedBooks.map(book => (
            <tr key={book.id}>
              <td>{book.title}</td>
              <td>{book.author}</td>
              <td>{book.year}</td>
              <td>{book.pages}</td>
              <td><img onClick={() => handleDeleteBook(book.id)} src={logoDelete} alt="" /></td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};