import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { Container } from "components/GlobalStyle";
import { AuthContainer, BackroundAuth, ContainerText, FormUl, LabelForm, RegisterButton, RegisterForm, RegisterH1, RegisterH2, RegisterIMG, RegisterInput, RegisterLi, RegisterP, RegisterUL } from "./StyledAuth/Register.styled";
import Logologin from './img/.svg';
import LineLogin from './img/Line 1.svg';
import { LineLoginIMG, LoginAuthor, LoginContainer, LoginIMG, LoginText } from "./StyledAuth/Login.styled";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { fetchUsers, login, register } from "../../redux/auth/operation";

export const Login = () => {
   const dispatch = useDispatch();
   const users = useSelector(state => state.auth.items);
   useEffect(() => {
      dispatch(fetchUsers());
   }, [dispatch]);

   const handleSubmit = (e) => {
      e.preventDefault();
      const form = e.currentTarget;
      const email = form.email.value;
      const password = form.password.value;
      
      // ,      email
      const user = users.find(user => user.email === email);
      console.log(user);
   
      if (!user) {
         //     email  ,    
         toast.error("   email  !");
         return;
      }
   
      //   
      if (user.password !== password) {
         //    ,    
         toast.error(" !");
         return;
      }
   
      //     ,  
      dispatch(login({
         id: user.id,
         name: user.name,
         email: email,
         password: password,
      }))
         .then(() => {
            console.log(" !");
            // :  -     
         })
         .catch((error) => {
            console.error("  :", error);
            // :  
         });
   };

   return (
      <section>
         <Container>
            <BackroundAuth>
               <LoginContainer>
                  <RegisterForm onSubmit={handleSubmit}>
                     <LabelForm>
                          <span>*</span>
                        <RegisterInput
                           id="email"
                           name="email"
                           placeholder="E-mail"
                           type="email"
                        />
                     </LabelForm>
                     <LabelForm>
                         <span>*</span>
                        <RegisterInput
                           id="password"
                           name="password"
                           placeholder=""
                           type="password"
                        />
                     </LabelForm>
                     <RegisterButton type="submit"></RegisterButton>
                  </RegisterForm>
                  <FormUl>
                     <li><NavLink to='/'></NavLink></li>
                  </FormUl>
               </LoginContainer>
            </BackroundAuth>
            <ContainerText>
               <LoginIMG src={Logologin} alt="" />
               <LoginText>
                   -   ,              .
               </LoginText>
               <LineLoginIMG src={LineLogin} alt="" />
               <LoginAuthor> .</LoginAuthor>
            </ContainerText>
         </Container>
         <ToastContainer
            position="top-right"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="light"
         />
      </section>
   );
};